<template>
  <div class="pay">
    <van-nav-bar title="提交订单" left-text left-arrow @click-left="onClickLeft" />
    <van-cell is-link @click="showPopup">
      选择收货地址
      <div class="address-content">
        <div class="name">{{currentAddress.name}}</div>
        <div class="phone">{{currentAddress.tel}}</div>
        <div class="address-info">{{currentAddress.address}}</div>
      </div>
    </van-cell>
    <van-popup v-model="show" position="bottom" round lock-scroll closeable>
      <van-address-list
        v-model="chosenAddressId"
        :list="list"
        @select="selectAddress"
        @add="newAddress"
      />
    </van-popup>

    <div class="order-info">
      <div class="order-box">
        <div class="order-title">订单信息</div>

        <div>
          <div class="cell-box" v-for="(item, index) in shopbagData" :key="index">
            <div class="pro-img">
              <img class="auto-img" :src="item.small_img" alt />
            </div>
            <div class="pro-info">
              <div class="text-box">
                <div class="text-name">{{item.name}}</div>
                <div class="text-enname">{{item.enname}}</div>
                <div class="text-rule">{{item.rule}}</div>
              </div>
              <div class="price-box">
                <div class="fl price">
                  ￥{{item.price}}
                  <span>x{{item.count}}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <van-submit-bar
      label="合计"
      :price="product.total * 100"
      button-text="去支付"
      tip-icon="info-o"
      @submit="onSubmit"
      button-color="#0c34ba"
    />
  </div>
</template>

<script>
import "../assets/less/pay.less";
import { utils } from "../assets/js/utils";
export default {
  data() {
    return {
      show: false,
      chosenAddressId: "",
      currentAddress: {
        name: "",
        tel: "",
        address: "",
      },
      list: [],
      shopbagData: [],
      sids: [],
      product: {
        count: 0,
        total: 0,
      },
    };
  },
  created() {
    this.sids = this.$route.query.sids.split("-");
    this.getReceiveAddress();
    this.getShopbagDataBySids();
  },
  methods: {
    onClickLeft() {
      this.$router.push({ name: "Shopbag" });
    },
    showPopup() {
      this.show = true;
    },
    selectAddress(item, index) {
      this.show = false;
      this.isOpen = false;

      for (let key in this.currentAddress) {
        this.currentAddress[key] = item[key];
      }
    },
    // 获取地址
    getReceiveAddress() {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/findAddress",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
           
          if (result.data.code == 20000) {
            result.data.result.map((v) => {
              let address = v.province + v.city + v.county + v.addressDetail;
              this.list.push({
                id: v.aid,
                name: v.name,
                tel: v.tel,
                address,
                isDefault: Boolean(v.isDefault),
              });

              if (Boolean(v.isDefault)) {
                (this.chosenAddressId = v.aid),
                  (this.currentAddress.name = v.name);
                this.currentAddress.tel = v.tel;
                this.currentAddress.address = address;
              }
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
           
        });
    },
    // 获取订单信息
    getShopbagDataBySids() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/commitShopcart",
        params: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(this.sids),
        },
      })
        .then((result) => {
          this.$toast.clear();
           
          if (result.data.code == 50000) {
            result.data.result.map((v) => {
              this.product.count += v.count;
              this.product.total += v.count * v.price;
            });

            this.shopbagData = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
           
        });
    },
    newAddress() {
      this.$router.push({ name: "NewAddress" });
    },
    //立即结算
    onSubmit() {
      //判断是否选择地址
      if (this.chosenAddressId == "") {
        this.$toast("请选择收货地址");
        return;
      }

      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      //参数序列化
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        sids: JSON.stringify(this.sids),
        phone: this.currentAddress.tel,
        address: this.currentAddress.address,
        receiver: this.currentAddress.name,
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/pay",
        data,
      })
        .then((result) => {
          this.$toast.clear();

          this.$toast(result.data.msg);
          if (result.data.code == 60000) {
            this.$router.push({ name: "Order" });
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
  },
};
</script>

<style lang="less" scoped>
</style>